<template>
    <div class="main" v-if="mainPage===true">
      <div class="main__bg" :style="bgMainImage"></div>
      <div class="main__wrapper">
        <div class="main__header clearfix">
          <div class="main__logo float-left mr-2"><img width="32" :src="quizdata.company_logo" /></div>
          <div class="main__name">{{ quizdata.company_name }}</div>
        </div>
        <div class="main__body">
          <h1 class="main__title my-2">{{ quizdata.title }}</h1>
          <div class="main__desc my-2">{{ quizdata.desc }}</div>
          <div class="main__actions my-2">
            <a class="main__button btn btn-error" href="#" @click="mainPage=false">{{ quizdata.button_text }}</a>
          </div>
        </div>
        <div class="main__footer">
          <div class="main__phone">{{ quizdata.company_phone }}</div>
          <div class="main__phone-desc">{{ quizdata.company_phone_desc }}</div>
        </div>
      </div>
    </div>

    <div class="quiz container grid-lg" v-else-if="mainPage===false">
        <div class="quiz__header clearfix">
          <div class="quiz__logo float-left mr-2"><img width="32" :src="quizdata.company_logo" /></div>
          <div class="quiz__title">{{ quizdata.subtitle }}</div>
        </div>
        <div class="quiz__steps" v-for="(step, stepNumber) in quizdata.steps" v-if="checkStep(stepNumber)">
            <div class="quiz__operator" v-if="checkOperator(step.desc)">
              <div class="tile">
                <div class="tile-icon">
                  <img :src="quizdata.operator_image" class="avatar avatar-md badge" />
                </div>
                <div class="tile-content">
                  <div class="tile-title">{{ quizdata.operator_name}}</div>
                  <small class="tile-subtitle">{{ quizdata.operator_post }}</small>
                </div>
              </div>
              <blockquote class="quiz__operator-quote text-small">{{ step.desc }}</blockquote>
            </div>
            <div class="quiz__step">
                <h2 class="quiz__step-title">{{ step.title }}</h2>
                <div class="quiz__question" v-for='item in step.questions'>

                    <div v-if='checkType(item.type, "radio")' class="quiz__label" :class="{'required': item.required}">{{ item.label }}</div>
                    <div v-if='checkType(item.type, "radio")' class="quiz__radio columns">
                      <div class="column col-4 col-sm-6 col-xs-12" v-for="variant in item.answers">
                        <label class="quiz__variant card my-2">
                            <div class="card-image">
                              <img v-if="variant.image" :src="variant.image" class="quiz__variant-image img-responsive">
                            </div>
                            <div class="card-header">
                              <input type="radio" v-model="answers[item.name]" v-bind:value='variant.title' v-bind:name="item.name">
                              <span class="quiz__variant-title">&nbsp;{{ variant.title }}</span>
                            </div>
                        </label>
                      </div>
                      <p class="quiz__question-desc text-small" v-if="item.desc">{{ item.desc }}</p>
                    </div>

                    <div v-if='checkType(item.type, "number")' class="quiz__number">
                        <label>
                            <div class="quiz__label" :class="{'required': item.required}">{{ item.label }}</div>
                            <input type="number" :min="item.min" :placeholder="item.placeholder" v-model.number="answers[item.name]" v-bind:name="item.name">
                            <p class="quiz__question-desc text-small" v-if="item.desc">{{ item.desc }}</p>
                        </label>
                    </div>

                    <div v-if='checkType(item.type, "text")' class="quiz__text">
                        <label>
                            <div class="quiz__label" :class="{'required': item.required}">{{ item.label }}</div>
                            <input type="text" v-model.trim="answers[item.name]" v-bind:name="item.name" :placeholder="item.placeholder">
                            <p class="quiz__question-desc text-small" v-if="item.desc">{{ item.desc }}</p>
                        </label>
                    </div>

                    <div v-if='checkType(item.type, "contacts")' class="quiz__contacts">
                        <label>
                            <div class="quiz__label" :class="{'required': item.required}">{{ item.label }}</div>
                            <input type="text" v-model.trim="answers[item.name]" v-bind:name="item.name" :placeholder="item.placeholder">
                            <p class="quiz__question-desc text-small" v-if="item.desc">{{ item.desc }}</p>
                        </label>
                    </div>
                    <div v-if='checkType(item.type, "phone")' class="quiz__phone">
                        <label>
                            <div class="quiz__label" :class="{'required': item.required}">{{ item.label }}</div>
                            <input type="text" v-model.trim="answers[item.name]" v-bind:name="item.name" :placeholder="item.placeholder" @input="phoneNumber(item.name)">
                            <p class="quiz__question-desc text-small" v-if="item.desc">{{ item.desc }}</p>
                        </label>
                    </div>

                </div>
            </div>
        </div>
        <div class="quiz__actions my-2">
            <div class="toast toast-error my-2" v-if="errorsList.length">
              <div class="quiz__error" v-for="error in errorsList">{{ error }}</div>
            </div>
            <button class="btn quiz__prev-button mr-2" v-if='showPrev()' @click='prev'>Назад</button>
            <button class="btn quiz__next-button mr-2" v-if='showNext()' @click='next'>Далее</button>
            <button class="btn quiz__submit-button btn-error mr-2" v-if='showSubmit()' @click='sendData' :disabled="agree === false">Отправить запрос</button>
            <div class="quiz__agree d-inline-block" v-if='showSubmit()'>
              <input type="checkbox" class="quiz__agree mr-2" required v-model='agree' /> С политикой конфиденциальности ознакомлен(а)
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'Quiz',
  props: ['quizdata', 'scriptid'],
  data: function () {
    return {
      mainPage: true,
      errorsList: [],
      currentStep: 0,
      agree: true,
      bgMainImage: {
        backgroundImage: 'url(' + this.quizdata.background_image + ')'
      },
      // https://script.google.com/macros/s/AKfycbyn9cAT01eX7Hi3pripfTk_WrrNDZhfvaXkJfIXbbgyv1a7XUk/exec";
      postUrl: 'https://script.google.com/macros/s/' + this.scriptid + '/exec',
      answers: {}
    }
  },
  methods: {
    sendData: function () {
      this.errorsList = []
      this.saveData()
      if (!this.errorsList.length) { 
        var dataToDB = new FormData()
        var redirect = this.quizdata.redirect
        dataToDB.append('JSONdata', JSON.stringify(this.answers))
        this.axios.post(this.postUrl, dataToDB).then(function (response) {
          console.log(response)
          window.location.href = redirect
        }).catch(function (error) {
          console.log(error)
        })
      }
    },
    validate() {
      var questions = this.quizdata.steps[this.currentStep].questions
      var answers = this.answers
      var errors = []
      questions.forEach( function (question, n) {
        var questionName = questions[n].name
        var value = answers[questionName]
        if (question.required) {
          if (question.type === 'radio' && !value) {
            errors.push('Нужно выбрать "' + question.label + '"')
          }
          else if (!value) {
            errors.push('Поле "' + question.label + '" не может быть пустым')
          }
        }
        if (question.type === 'number' && !(question.min<parseFloat(value) && parseFloat(value)<question.max)) {
          errors.push('Поле "' + question.label + '" должно быть в диапазоне от ' + question.min + ' до ' + question.max)
        }
        if (question.type === 'phone' && value.length !== 15) {
          errors.push('Правильно укажите мобильный телефон')
        }
      })
      this.errorsList = this.errorsList.concat(errors)
    },
    phoneNumber(name) {
      var x = this.answers[name].replace(/\D/g, '').match(/([7-8]{0,1})(\d{0,3})(\d{0,3})(\d{0,4})/)
      this.answers[name] = !x[2] ? '+' + x[1] : '+' + x[1] + ' ' + x[2] + ' ' + x[3] + ' ' + x[4]
    },
    progressActive: function (stepNumber) {
      if (this.currentStep === stepNumber ) { return "active" }
    },
    checkStep: function (stepNumber) {
      return stepNumber === this.currentStep
    },
    checkType: function (item, type) {
      return item === type
    },
    checkOperator: function (desc) {
      return desc && desc.length > 0
    },
    saveData: function () {
      this.validate()
      localStorage.answers = JSON.stringify(this.answers)
    },
    showPrev: function () {
      return this.currentStep > 0
    },
    showNext: function () {
      return this.currentStep < (this.quizdata.steps.length - 1)
    },
    showSubmit: function () {
      return this.currentStep === (this.quizdata.steps.length - 1)
    },
    prev: function () {
      this.saveData()
      this.errorsList = []
      if (this.showPrev()) { this.currentStep-- };
    },
    next: function () {
      this.errorsList = []
      this.saveData()
      if (this.showNext() && !this.errorsList.length) { this.currentStep++ }
    },
    setStep: function (stepNumber) {
      this.currentStep = stepNumber
    },
    requiredValidate: function () {

    }
  },
  mounted () {
    console.log(this.quizdata)
    if (localStorage.answers) {
      this.answers = JSON.parse(localStorage.answers)
    }
  }
}
</script>

